<template>
  <div>
    <form v-on:submit.prevent>
      <!--<div :class="{'form-group':true, 'has-error':errors.has('')}">-->
        <!--<label></label>-->
        <!--<input type="text" class="form-control" placeholder="" v-model="match.sinaLiveUrl" name="">-->
        <!--<span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>-->
      <!--</div>-->
      <!--<div :class="{'form-group':true, 'has-error':errors.has('')}">-->
        <!--<label></label>-->
        <!--<input type="text" class="form-control" placeholder="" v-model="match.sinaShujuUrl" name="">-->
        <!--<span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>-->
      <!--</div>-->
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
      <label>2019-01-01</label>
      <input type="text" class="form-control" placeholder="" v-model="match.playDateStr"  v-validate="'required'" name="">
      <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
    </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label>12:30</label>
        <input type="text" class="form-control" placeholder="" v-model="match.playTime"  v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="match.name" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <!--<div :class="{'form-group':true, 'has-error':errors.has('')}">-->
        <!--<label></label>-->
        <!--<select class="form-control" name="" v-model="match.locked">-->
            <!--<option value="1"></option>-->
            <!--<option value="0"></option>-->
        <!--</select>-->
        <!--<span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>-->
      <!--</div>-->
      <!--<div :class="{'form-group':true, 'has-error':errors.has('')}">-->
        <!--<label></label>-->
        <!--<input type="date" placeholder="" v-model="match.unlockDateStr" name="">-->
        <!--<input type="time" placeholder="" v-model="match.unlockTimeStr" name="">-->
        <!--<span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>-->
      <!--</div>-->
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="match.project" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="match.game" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <!--<div :class="{'form-group':true, 'has-error':errors.has('')}">-->
        <!--<label></label>-->
        <!--<select class="form-control" name="" v-model="match.emphasis">-->
            <!--<option value="1"></option>-->
            <!--<option value="0"></option>-->
        <!--</select>-->
        <!--<span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>-->
      <!--</div>-->
      <h3></h3>
      <div>
        <div v-for="l in match.lives">
          <label></label>
          <input type="text" class="liveInput" v-model="l.name" style="width:200px">
          <label></label>
          <input type="text" class="liveInput" v-model="l.link">
          <div :class="{'form-group':true, 'has-error':errors.has('')}" style="width:200px">
            <label></label>
            <select class="form-control" name="" v-model="l.playFlag" style="width:150px">
              <option value="INNER"></option>
              <option value="OUTER"></option>
            </select>
            <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
          </div>
          <button v-on:click="removeLive(l)" class="del-image-btn"></button>
        </div>
        <button v-on:click="addLive"> + </button>
      </div>
      <!--<h3></h3>-->
      <!--<div>-->
        <!--<div v-for="ad in match.ads">-->
          <!--<label></label>-->
          <!--<input type="text" class="liveInput" v-model="ad.name">-->
          <!--<label></label>-->
          <!--<input type="text" class="liveInput" v-model="ad.link">-->
          <!--<br>-->
          <!--<label></label>-->
          <!--<input type="text" class="liveInput" v-model="ad.light">-->
          <!--<button v-on:click="removeAd(ad)" class="del-image-btn"></button>-->
        <!--</div>-->
        <!--<button v-on:click="addAd"> + </button>-->
      <!--</div>-->
      <button type="button" class="btn btn-default" v-on:click="submit()"></button>
    </form>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
import zhcn from 'vee-validate/dist/locale/zh_CN'
import VeeValidate, { Validator } from 'vee-validate'
import VueKindEditor from 'vue-kindeditor'
import 'kindeditor/kindeditor-all-min.js'
import 'kindeditor/themes/default/default.css'

Vue.use(VueResource)

Validator.localize('zh_CN', zhcn)
Vue.use(VeeValidate)

Vue.config.productionTip = false
Vue.use(VueKindEditor)

export default {
  name: 'Index',
  data () {
    return {
      match: {
        locked: 0,
        emphasis: 0,
        lives: [],
        ads: []
      }
    }
  },
  methods: {
    submit: function () {
      this.$validator.validateAll().then(result => {
        if (result) {
          let match = this.$data.match
          if (!match.id) {
            Vue.http.post(g.t('/api/admin/matchs'), JSON.stringify(match)).then(function (response) {
              g.href('/matchList')
            }, function (response) {
              g.showMessage(response)

            })
          } else {
            Vue.http.put(g.t('/api/admin/matchs/' + match.id), JSON.stringify(match)).then(function (response) {
              g.href('/matchList')
            }, function (response) {
              g.showMessage(response)
            })
          }
        }
      })
    },
    upload: function (e) {
      let that = this

      e.preventDefault()
      var files = e.target.files
      var data = new FormData()
      data.append('image', files[0])

      Vue.http.post(g.t('/api/admin/file'), data).then(function (response) {
        that.$set(that.$data.match, 'image', response.bodyText)
      }, function (response) {
        g.toLogin()
      })
    },
    addLive () {
      this.match.lives.push({'name': '', 'link': ''})
    },
    addAd () {
      this.match.ads.push({'name': '', 'link': '', light: 0})
    },
    removeLive (live) {
      this.match.lives.splice(this.match.lives.indexOf(live), 1)
    },
    removeAd (ad) {
      this.match.ads.splice(this.match.ads.indexOf(ad), 1)
    }
  },
  created () {
    let match = this.$data.match
    let matchId = this.$route.params.id
    if (matchId) {
      Vue.http.get(g.t('/api/admin/matchs/' + matchId)).then((response) => {
        let matchEntity = response.data
        this.$set(match, 'id', matchEntity.id)
        this.$set(match, 'name', matchEntity.name)
        this.$set(match, 'locked', matchEntity.locked)
      this.$set(match, 'playDateStr', matchEntity.playDateStr)
      this.$set(match, 'playTime', matchEntity.playTime)
        this.$set(match, 'unlockTime', matchEntity.unlockTime)
        this.$set(match, 'unlockDateStr', matchEntity.unlockDateStr)
        this.$set(match, 'unlockTimeStr', matchEntity.unlockTimeStr)
        this.$set(match, 'project', matchEntity.project)
        this.$set(match, 'game', matchEntity.game)
        this.$set(match, 'emphasis', matchEntity.emphasis)
        this.$set(match, 'lives', matchEntity.lives)
        this.$set(match, 'ads', matchEntity.ads)
      this.$set(match, 'sinaLiveUrl', matchEntity.sinaLiveUrl)
      this.$set(match, 'matchStreamUrl', matchEntity.matchStreamUrl)
      this.$set(match, 'sinaShujuUrl', matchEntity.sinaShujuUrl)
      }, function (response) {
        g.toLogin()
      })
    }
  }
}
</script>

<style scoped>
.liveInput{
  width: 40%
}
</style>



