<template>
  <div>
      <h2></h2>
      <form id="queryForm" class="form-inline" role="form">

          <div class="form-group">
              <label></label>
              <select class="form-control" name="search_EQ_project">
                  <option value=""></option>
                  <option></option>
                  <option></option>
                <option></option>
                <option></option>
                   <option></option>
              </select>
          </div>
          <div class="form-group">
              <label></label>
              <select class="form-control" name="search_EQ_game">
                  <option value=""></option>
                  <option v-for="g in games">{{g}}</option>
              </select>
          </div>
          <div class="form-group">
              <label></label>
              <input type="date" class="form-control" name="search_EQ_playDateStr">
          </div>
          <div class="form-group">
              <label></label>
              <input type="input" class="form-control" name="search_LIKE_name">
          </div>
          <!--<div class="form-group">-->
              <!--<label></label>-->
              <!--<select class="form-control" name="search_EQ_locked">-->
                  <!--<option value=""></option>-->
                  <!--<option value="1"></option>-->
                  <!--<option value="0"></option>-->
              <!--</select>-->
          <!--</div>-->
          <!--<div class="form-group">-->
              <!--<label></label>-->
              <!--<select class="form-control" name="search_EQ_emphasis">-->
                  <!--<option value=""></option>-->
                  <!--<option value="1"></option>-->
                  <!--<option value="0"></option>-->
              <!--</select>-->
          <!--</div>-->
          <button type="button" class="btn btn-primary" v-on:click="goto(0)"></button>
        <button type="button" class="btn btn-info" v-on:click="create()"></button>
      </form>
      <hr>

      <table class="table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <!--<th></th>-->
            <!--<th></th>-->
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="a in page.content">
            <td>{{ a.playDateStr }}</td>
            <td>{{ a.playTime }}</td>
            <td>{{ week(a) }}</td>
            <td>{{ a.name }}</td>
            <td>{{ a.game }}</td>
            <td>{{ a.project }}</td>
            <!--<td>{{ a.emphasis == 1 ? '' : '' }}</td>-->
            <!--<td>{{ a.locked == 1 ? '' : '' }}</td>-->
            <td>
              <button type="button" class="btn btn-info" v-on:click="edit(a)"></button>
              <button type="button" class="btn btn-danger" v-on:click="remove(a)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <nav>
        <ul class="pagination">
          <li><a @click="goto(0)" href="javascript:void(0);">&lt;&lt;</a></li>
          <li v-for="n in page.totalPages" v-if="n > page.number - 12 && n < page.number + 10" :class="{'active':n == page.number + 1}"><a href="javascript:void(0);" v-on:click="goto(n-1)">{{ n }}</a></li>
          <li><a @click="goto(page.totalPages-1)" href="javascript:void(0);">&gt;&gt;</a></li>
        </ul>
      </nav>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

export default {
  name: 'Index',
  data () {
    return {
      games: '|||||||NBA|CBA|||||||||||||||||||||K|J|||||||||| ||'.split('|'),
      page: {
        content: [],
        totalElements: 0,
        last: false,
        totalPages: 0,
        sort: null,
        size: 0,
        number: 0,
        first: false,
        numberOfElements: 0
      }
    }
  },
  methods: {
    create: function () {
      g.href('/matchForm/')
    },
    edit: function (match) {
      g.href('/matchForm/' + match.id)
    },
    remove: function (match) {
      var that = this
      Vue.http.delete(g.t('/api/admin/matchs/' + match.id)).then(function (response) {
        var matchs = that.$data.page.content
        matchs.splice(matchs.indexOf(match), 1)
      }, function (response) {
        g.toLogin()
      })
    },
    goto: function (page) {
      var formStr = $('#queryForm').serialize()
      var that = this
      Vue.http.get(g.t(`/api/admin/matchs/page?${formStr}&page=${page}`)).then(function (response) {
        that.$set(that.$data, 'page', response.data)
      }, function (response) {
        g.toLogin()
      })
    },
    liveNames: function (lives) {
      var liveNamesArray = lives.map((l) => l.name)
      return liveNamesArray.join(',')
    },
    week: function (match) {
      var day = new Date(match.playDate).getDay()
      var xingqi = ''
      switch (day) {
        case 0:
          xingqi = ''
          break
        case 1:
          xingqi = ''
          break
        case 2:
          xingqi = ''
          break
        case 3:
          xingqi = ''
          break
        case 4:
          xingqi = ''
          break
        case 5:
          xingqi = ''
          break
        case 6:
          xingqi = ''
          break
        default:
          xingqi = ''
      }
      return xingqi
    }
  },
  created () {
    this.goto(0)
  }
}
</script>

<style scoped>
</style>



