<template>
  <div>
    <form>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="liveSource.name" v-validate="'required'" name="" readonly>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="liveSource.link" v-validate="'required'" name="" readonly>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <select class="form-control" name="" v-model="liveSource.active">
            <option value="1"></option>
            <option value="0"></option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="number" class="form-control" placeholder="" v-model="liveSource.fetchInterval" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <!--<div :class="{'form-group':true, 'has-error':errors.has('')}">-->
        <!--<label>|</label>-->
        <!--<input type="text" class="form-control" placeholder="" v-model="liveSource.channels" v-validate="'required'" name="">-->
        <!--<span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>-->
      <!--</div>-->
      <!--<div :class="{'form-group':true, 'has-error':errors.has('')}">-->
        <!--<label>|</label>-->
        <!--<input type="text" class="form-control" placeholder="" v-model="liveSource.innerPlayChannels"  name="">-->
        <!--<span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>-->
      <!--</div>-->
      <button type="button" class="btn btn-default" v-on:click="submit()"></button>
    </form>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
import zhcn from 'vee-validate/dist/locale/zh_CN'
import VeeValidate, { Validator } from 'vee-validate'
import VueKindEditor from 'vue-kindeditor'
import 'kindeditor/kindeditor-all-min.js'
import 'kindeditor/themes/default/default.css'

Vue.use(VueResource)

Validator.localize('zh_CN', zhcn)
Vue.use(VeeValidate)

Vue.config.productionTip = false
Vue.use(VueKindEditor)

export default {
  name: 'Index',
  data () {
    return {
      liveSource: {},
      liveSources: []
    }
  },
  methods: {
    submit: function () {
      this.$validator.validateAll().then(result => {
        if (result) {
          let liveSource = this.$data.liveSource
          if (!liveSource.id) {
            Vue.http.post(g.t('/api/admin/liveSources'), JSON.stringify(liveSource)).then(function (response) {
              g.href('/liveSourceList')
            }, function (response) {
              g.toLogin()
            })
          } else {
            Vue.http.put(g.t('/api/admin/liveSources/' + liveSource.id), JSON.stringify(liveSource)).then(function (response) {
              g.href('/liveSourceList')
            }, function (response) {
              g.toLogin()
            })
          }
        }
      })
    },
    upload: function (e) {
      let that = this

      e.preventDefault()
      var files = e.target.files
      var data = new FormData()
      data.append('image', files[0])

      Vue.http.post(g.t('/api/admin/file'), data).then(function (response) {
        that.$set(that.$data.liveSource, 'image', response.bodyText)
      }, function (response) {
        console.log(2)
        console.log(response.bodyText)
      })
    },
    onContentChange (val) {
      this.liveSource.content = val
    }
  },
  created () {
    let liveSource = this.$data.liveSource
    let liveSourceId = this.$route.params.id
    if (liveSourceId) {
      Vue.http.get(g.t('/api/admin/liveSources/' + liveSourceId)).then((response) => {
        let liveSourceEntity = response.data
        this.$set(liveSource, 'id', liveSourceEntity.id)
        this.$set(liveSource, 'name', liveSourceEntity.name)
        this.$set(liveSource, 'link', liveSourceEntity.link)
        this.$set(liveSource, 'active', liveSourceEntity.active)
        this.$set(liveSource, 'fetchInterval', liveSourceEntity.fetchInterval)
        this.$set(liveSource, 'channels', liveSourceEntity.channels)
      this.$set(liveSource, 'innerPlayChannels', liveSourceEntity.innerPlayChannels)
      }, function (response) {
        g.toLogin()
      })
    }

    // 
    let liveSources = this.$data.liveSources
    Vue.http.get(g.t('/api/admin/liveSources')).then((response) => {
      liveSources.splice(0, liveSources.length)
      response.data.forEach(at => liveSources.push(at))
    }, function (response) {
      g.toLogin()
    })
  }
}
</script>

<style scoped>
body{
  background-color: #CCC;
}
</style>



