<template>
  <div>
      <h2></h2>
      <div><button type="button" class="btn btn-info pull-right" v-on:click="create()"></button></div>
      <table class="table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="a in page.content">
            <td>{{ a.id }}</td>
            <td>{{ a.adKey }}</td>
            <td>{{ a.message }}</td>
            <td><a :href="a.link" target="_blank"></a></td>
            <td><a :href="a.image" target="_blank"></a></td>
            <td>
              <button type="button" class="btn btn-info" v-on:click="edit(a)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <nav>
        <ul class="pagination">
          <li><a @click="goto(0)" href="javascript:void(0);">&lt;&lt;</a></li>
          <li v-for="n in page.totalPages" v-if="n > page.number - 12 && n < page.number + 10" :class="{'active':n == page.number + 1}"><a href="javascript:void(0);" v-on:click="goto(n-1)">{{ n }}</a></li>
          <li><a @click="goto(page.totalPages-1)" href="javascript:void(0);">&gt;&gt;</a></li>
        </ul>
      </nav>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

export default {
  name: 'Index',
  data () {
    return {
      games: '|||||||NBA|CBA'.split('|'),
      page: {
        content: [],
        totalElements: 0,
        last: false,
        totalPages: 0,
        sort: null,
        size: 0,
        number: 0,
        first: false,
        numberOfElements: 0
      }
    }
  },
  methods: {
    create: function () {
      g.href('/pageAdForm/')
    },
    edit: function (pageAd) {
      g.href('/pageAdForm/' + pageAd.id)
    },
    remove: function (pageAd) {
      var that = this
      Vue.http.delete(g.t('/api/admin/pageAds/' + pageAd.id)).then(function (response) {
        var pageAds = that.$data.page.content
        pageAds.splice(pageAds.indexOf(pageAd), 1)
      }, function (response) {
        g.toLogin()
      })
    },
    goto: function (page) {
      var formStr = $('#queryForm').serialize()
      var that = this
      Vue.http.get(g.t(`/api/admin/pageAds/page?${formStr}&page=${page}`)).then(function (response) {
        that.$set(that.$data, 'page', response.data)
      }, function (response) {
        g.toLogin()
      })
    }
  },
  created () {
    this.goto(0)
  }
}
</script>

<style scoped>
</style>



